@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: #5E9CB9;
  font-family: 'Raleway', sans-serif;
  color: #5E9CB9
}

i{
  display: inline;
  font-size: 25px;
  margin-right: 10px;
}

.App {
  width: 550px;
  margin: auto;
  top: 150px;
  left: 0;
  right: 0;
  position: absolute;
  height: auto;
}

#buttons{
  color: white;
  margin: 40px 0 0;
}

.content{
  margin: 40px;
  display: inline-block;
  width: 470px;
}

.buttons{
  color: white;
  background-color: #5E9CB9;
}

.posts{
  width: 45px;
  height: 40px;
  margin: 0 5px;
  font-size: 20px;
}

.coder{
  margin: 20px;
  text-align: center;
}

#new-quote{
  height: 40px;
  margin-right: 40px;
  display:inline-block;
  position: absolute;
  right: 0;
  font-size: 17px;
}

#author{
  text-align: right;
  font-size: 18px;
  margin: 20px 10px 10px;
}

#text-box{
  display: inline-block;
}

#text{
  font-size: 25px;
  display: inline;
}

#quote-box{
  border: solid 1px white;
  border-radius: 2px;
  background-color: white;
  display: inline-block;
